import * as React from 'react';
import { Route } from 'react-router-dom';

import LobbyScreen from '../../containers/screen/LobbyScreen';

import MultiGameScreen from '../../containers/screen/MultiGameScreen';
import SingleGameScreen from '../../containers/screen/SingleGameScreen';

class TicTacToe extends React.Component {
  public render() {
    return (
        <div>
            <Route exact={true} path={process.env.PUBLIC_URL + "/"} component={LobbyScreen}/>
            <Route path={process.env.PUBLIC_URL + "/single"} component={SingleGameScreen}/>
            <Route path={process.env.PUBLIC_URL + "/multi"} component={MultiGameScreen}/>
        </div>
    );
  }
}

export default TicTacToe;


